ul {
  list-style: none;
}

.petsListWrapper {
  justify-content: center;
  width: inherit;
  display: grid;
  grid-gap: 32px;
  margin-top: 24px;
  margin-left: -5px;
}

@media screen and (min-width: 768px) {
  .petsListWrapper {
    grid-template-columns: repeat(auto-fill, minmax(336px, 1fr));
    margin-top: 40px;
  }
}

@media screen and (min-width: 1280px) {
  .petsListWrapper {
    grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
    margin-top: 42px;
  }
}

.section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  margin-left: auto;
  margin-right: auto;

  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 480px) {
  .section {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .section {
    width: 768px;
  }
}

@media screen and (min-width: 1280px) {
  .section {
    padding-left: 18px;
    padding-right: 18px;
    width: 1280px;
  }
}
